<template>
  <div>
		<video loop autoplay ref="play" @click="play" src="../../assets/media/001.mp4"></video>
  </div>
</template>
<style scoped>
	video {
		position: absolute;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
	}
</style>
<script>
 

  export default {
    data(){
      return {
				isPlay: true,
      }
    },
    methods: {
			play(){
				if(this.isPlay){
					this.$refs.play.pause()
				}else{
					this.$refs.play.play()
				}
				this.isPlay = !this.isPlay
			}
    },
    mounted(){

    },
    created(){
      
    }
  }
</script>



